import React from 'react';
import style from './FriendList.module.css'

const FriendList = ({ friends }) => {
  return (
    <ul className={style.friendList}>
          {friends.map(num => (
              <li className={style.item} key={num.id}>
                  <span className={ num.isOnline  ? style.status : style.offline}></span>
          <img className={style.avatar} src={num.avatar} alt={num.name} width="48" />
          <p className="name">{num.name}</p>
        </li>
      ))}
    </ul>
  );
};
export default FriendList;